* {
    font-family: 'Open Sans', sans-serif !important;
}

nav {
  height: 60px;
}

.full-container {
  min-height: 100vh;
  /* background: linear-gradient(to right, rgb(54, 20, 166) 0%, rgb(33, 162, 233) 100%); */
  background: -webkit-gradient(linear, left top, right top, from(rgb(232, 246, 244)), to(rgb(237, 247, 252)));
  background: -webkit-linear-gradient(left, rgb(232, 246, 244) 0%, rgb(237, 247, 252) 100%);
  background: -o-linear-gradient(left, rgb(232, 246, 244) 0%, rgb(237, 247, 252) 100%);
  background: linear-gradient(to right, rgb(232, 246, 244) 0%, rgb(237, 247, 252) 100%);
}

@media screen and (max-width: 480px) {
  .uk-navbar-center {
    display: none
  }
}



